		border: 1px solid #fff;
		font-weight: 700;
	}
	body.sticky-menu-group-nav #menu-group-nav li a:focus {
		border: 3px solid lightgray;
	}
	body.sticky-menu-group-nav #menu-group-nav li a {
		color: #fff;
